@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,600;1,100&family=Roboto+Mono:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body{
  --gradient: linear-gradient(45deg, #000000, #030039, #220000, #171717);
  background-image: var(--gradient);
  background-size: 400%;
  -webkit-animation: bg-animation 14s infinite alternate;
          animation: bg-animation 14s infinite alternate;
  /* text-align: center; */
  height: 100vh;
  width: 100vw;

}

@-webkit-keyframes bg-animation {
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}

@keyframes bg-animation {
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}
.visible-moving {
  background: url(/pathfinders/static/media/perci2.3068d508.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.visible-fixed {
  background: url(/pathfinders/static/media/transparent.a7df3522.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.visible-moving,
.visible-fixed,
.not-visible {
  width: 70px;
  height: 70px;
  /* border: 1px solid black; */
}

.grid {
  width: 100vw;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  background-image: url(/pathfinders/static/media/Mars_Valles_Marineris.b231bdf0.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  bottom: 5%;
  z-index: -999;

}

