.battery-section {
  color: antiquewhite;
  font-family: 'Poppins', sans-serif;
  padding: 1em 0 1.5em 1em;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 12px;
  text-align: left;
}

.score-section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: 24px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

/* QUESTION/TIMER/LEFT SECTION */
.question-section {
  width: 100%;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
  font-family: 'Roboto Mono', monospace;
  color: #f69c34;
  font-size: 28px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 5px;
  padding-bottom: 0.5em;
  text-align: center;
}

.question-count span {
  font-size: 28px;
 
}

.question-text {
  margin-bottom: 1.5em;
  font-family: 'Poppins', sans-serif;
  color: antiquewhite;
  letter-spacing: 0.1em;
  font-weight: 600;
  padding: 0 0.5em;
  text-align: center;
}

.timer-text {
  background: rgb(230, 153, 12);
  padding: 15px;
  margin-top: 20px;
  margin-right: 20px;
  border: 5px solid rgb(255, 189, 67);
  border-radius: 15px;
  text-align: center;
}

/* ANSWERS/RIGHT SECTION */
.answer-section {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

button {
  width: 80%;
  font-size: 16px;
  color: antiquewhite;
  background-color: rgb(194, 50, 6);
  border-radius: 0.5em;
  display: -webkit-flex;
  display: flex;
  padding: 0.5em;
  margin-bottom: 0.5em;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border: 4px solid rgba(194, 50, 6, 0.13);
  cursor: pointer;
  /* z-index: 999; */
}

.correct {
  background-color: #2f922f;
}

.incorrect {
  background-color: #ff3333;
}

button:hover {
  background-color: #555e7d;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}

.message-container{
        text-align: center;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        overflow: hidden;
        -webkit-align-items: center;
                align-items: center;
        margin: 3em 0;
      
}

p.par-mess{
    margin-bottom: 2em;
}
.card-container{
  width: 80vw;
  height: 100vh;
  text-align: center;
  border-radius: 6px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow: hidden;
  border-radius:2em;
  box-shadow: 0px 1rem 1.5rem rgba(black, 0.5);
  background-repeat: no-repeat;
  background-color: rgb(233, 233, 233);

}

h2.card-title{
  color:#012741;
  text-align: center;
  padding: 0; 
  margin-top: 2.5em;
  font-family: 'Roboto Mono', monospace;
  font-size: 24px;
  text-transform: uppercase;
  font-weight: 600;
  margin-bottom: 0.5em;

}

.card-paragraph{
    color: black;
    text-align: center;
    font-size: 16px;
    padding: 0 0.5em;
    text-transform: none;
    font-family: 'Poppins', sans-serif;
    line-height: 1.5em;
    margin-bottom: 1.2em;
    
}

.card-banner{

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    -webkit-justify-content: center;
            justify-content: center;
    box-sizing: border-box;
    margin-bottom: 1em;
    
}

.card-icon{
    background-color: white;
    width: 6rem;
    height: 6rem;
    box-shadow: 0 0.5rem 1rem rgba(black, 0.3);
    border-radius: 50%;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
    transition: -webkit-transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
    transition: transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
    transition: transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28), -webkit-transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
    margin-top: -5em;
}

.card-article{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.card-btn{
        font-weight: bold;
        background-color: rgb(194, 50, 6);
        text-align: center;
        width: 60%;
        border: none;
        padding: 0.5rem;
        outline: none;
        box-sizing: border-box;
        border-radius: 0.5em;
        -webkit-align-items: center;
                align-items: center;
        font-family: 'Poppins', sans-serif;
        font-size: 14px;
       
}
.card-btn:hover{
          background-color: #efb10a;
          -webkit-transform: scale(1.1);
                  transform: scale(1.1);
}

.card-btn:active{
          background-color: #e8a200;
          -webkit-transform: scale(1);
                  transform: scale(1);
}



